import React from 'react';

const contact = (props) => {
    return (
        <section id="contact">
            <h2></h2>
            <form>
                <input type="text" placeholder="" required/>
                <input type="email" placeholder="" required/>
                <textarea placeholder="" />
                <button type="submit" className="btn"></button>
            </form>
        </section>
    );
}

export default contact;


